body {
  font-family: Arial, sans-serif;
  background-color: #f4f4f4;
  margin: 0;
  padding: 20px;
}

h1 {
  margin: 0;
}

.currency_container {
  display: block;
}

.equals {
  margin: 0 20px;
  font-size: 24px;
  text-align: center;
}

.loader {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  border: 8px solid #f3f3f3; 
  border-top: 8px solid #3498db; 
  border-radius: 50%;
  width: 60px;
  height: 60px;
  animation: spin 1s ease-in-out infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.error_message {
  color: red;
  text-align: center;
}
